.main-content {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: row;
    flex-wrap: wrap;

    &__image-container {
        margin-left: auto;
        margin-right: auto;
        padding: 3rem 0 0;
 }        // background-color: black


    &__title {
 }        // padding: 1.2rem 0

    &__section {
        flex: 1 1 50%;
        margin-left: auto;
        margin-right: auto;
        // background-color: red

        @include at-medium {
            padding-left: 3rem; } }

    &__paragraphs {
        line-height: 1.65;
        margin: 1.8rem 0;

        @include at-small {
            font-size: 1.125rem; }

        &--bold {
            font-weight: bold; } }

    &__a-tag {
        font-weight: bold; } }
